body,html{margin:0;padding:0;font-family:Arial,sans-serif}.calls-container{display:flex;justify-content:space-between;padding:1.5rem 1rem;background-color:#113448;color:#fff;height:100vh;font:400 1.5rem "Open Sans",sans-serif;gap:1rem}.calls-container .section-title{text-align:flex-start}.calls-container .calls-form,.calls-container .calls-info{display:flex;flex-direction:column;width:50%;padding-top:2.5rem;align-content:center;box-sizing:border-box;position:relative}.calls-container .calls-info{margin-top:-3rem;padding:0 1rem 0 5rem}.calls-container .calls-info h2{color:#fff;margin:0 0 10px -10px;text-align:left}.calls-container .calls-info p{font-size:17px;margin:0 0 10px;color:#dfe5ec}.calls-container .calls-info ul{list-style-type:none;padding:0;font-size:20.5px;margin:0 0 10px}.calls-container .calls-info ul li{position:relative;margin-bottom:5px;color:#dfe5ec;padding-left:2rem;font-size:17px}.calls-container .calls-info ul li::before{content:"■";color:#14bf98;font-size:1rem;position:absolute;left:0;top:.2rem}.calls-container .calls-form{padding:0 5rem 0 1rem;box-sizing:border-box;position:relative;gap:.7rem}.calls-container .calls-form input[type=text],.calls-container .calls-form input[type=email],.calls-container .calls-form select,.calls-container .calls-form button{display:block;padding:1rem 1.3125rem;line-height:2rem;margin-bottom:10px;border:none;border-radius:5px;width:100%;cursor:text;color:#fff}.calls-container .calls-form #name,.calls-container .calls-form #phone,.calls-container .calls-form #email{color:#fff}.calls-container .calls-form input[type=text],.calls-container .calls-form input[type=email],.calls-container .calls-form select{border:1px solid #39728f;background-color:#2a5d77;color:#fff}.calls-container .calls-form button{background-color:#14bf98;color:#fff;cursor:pointer;width:100%}.calls-container .calls-form button:hover{background-color:rgba(0,0,0,0);color:#14bf98;border:1px solid #14bf98}.checkbox-container{display:block;position:relative;padding-left:35px;margin-bottom:12px;cursor:pointer;font-size:16px;user-select:none}.checkbox-container input{position:absolute;opacity:0;cursor:pointer;height:0;width:0}.checkbox-container .checkmark{position:absolute;top:0;left:0;height:25px;width:25px;background-color:#fff}.checkbox-container input:checked~.checkmark{background-color:#484a46}.checkbox-container .checkmark:after{content:"";position:absolute;display:none}.checkbox-container input:checked~.checkmark:after{display:block}.checkbox-container .checkmark:after{left:9px;top:5px;width:5px;height:10px;border:solid #fff;border-width:0 3px 3px 0;transform:rotate(45deg)}